<template>
  <div class="chart">
    <div class="header">
      <span class="vertical" :style="{ width: `${height + 20}px` }">{{ title }}</span>
    </div>
    <svg :width="width + 'px'" :height="`${height + 20}px`">
      <g v-for="({x, date, stacks, month, tick}, index) in bars" :key="index">
        <rect
          class="base"
          fill="transparent"
          :x="x + 'px'"
          :y="oy + 'px'"
          :width="barWidth + 'px'"
          :height="height + 'px'"
        />
        <rect
          :key="measure"
          class="bar"
          :class="measure"
          :fill="'#eeeeee'"
          :x="x + 'px'"
          :y="stacks[measure].y + 'px'"
          :width="stacks[measure].w + 'px'"
          :height="stacks[measure].h + 'px'"
          v-for="measure in selectedMeasures"
        />
        <line
          class="tickMonth"
          v-if="month"
          :x1="`${x + barWidth / 2}px`"
          :y1="`${oy + height}px`"
          :x2="`${x + barWidth / 2}px`"
          :y2="`${dateY - 9}px`"
        />
        <text
          v-if="month"
          :x="`${x + barWidth / 2}px`"
          :y="`${dateY}px`"
          width="100px"
          style="text-anchor: middle;"
        >{{ month }}</text>
        <line
          v-if="tick"
          class="tick"
          :x1="`${x + barWidth / 2}px`"
          :y1="`${oy + height}px`"
          :x2="`${x + barWidth / 2}px`"
          :y2="`${oy + height + 4}px`"
        />
      </g>
    </svg>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import moment from "moment";
import { reduce, each } from "lodash";
import { measures } from '../libs/common';

export default {
  name: "Chart",
  props: {
    countriesSelected: Array,
    selectedMeasures: Array,
    title: String,
    height: Number,
    width: Number
  },
  data() {
    return {
      tickLength: 3,
      gap: 12,
      dateY: 0,
      ox: 10,
      oy: 0,
      barWidth: 0,
      dateRange: [],
      dateNum: 0,
      bars: []
    };
  },
  computed: {
    ...mapGetters([
      'dates',
      'series',
      'seriesStats',
      'currentDate',
      'currentCountries'
    ])
  },
  methods: {
    calcContext() {
      this.dateRange = this.dates;
      this.dateNum = this.dates.length;
      let barWidth = Math.floor(this.width / this.dateNum);
      if (barWidth % 2 === 0) {
        barWidth -= 1;
      }
      barWidth -= 1;
      this.barWidth = barWidth;
    },
    calcBars() {
      const { height, barWidth, ox, oy, selectedMeasures, dates } = this;
      let x = ox;
      const bars = [];

      each(dates, (date, index) => {
        let y = oy + height;
        const stacks = {};

        const m = moment(date);
        const dateOfMonth = m.date();
        const dayOfWeek = m.day();
        let month = null;
        let tick = false;

        if (dateOfMonth === 1) {
          month = m.format("MMM");
        }
        if (dayOfWeek === 1) {
          // Monday
          tick = true;
        }
        let max = 0
        each(selectedMeasures, measure => {
          max += this.seriesStats[measure].maxSum;
        })

        each(selectedMeasures, measure => {
          const v = this.series[index].sum[measure];
          const h = Math.ceil((v * height) / max);
          y -= h;
          stacks[measure] = {
            x,
            y,
            w: barWidth,
            h,
            date
          };
        });

        const bar = {
          date,
          x,
          stacks,
          month,
          tick
        };
        bars.push(bar);
        x += barWidth + 1;
      });
      this.bars = bars;
    },
  },
  watch: {
    seriesStats () {
      this.dateY = this.oy + this.height + this.tickLength + this.gap;
      this.calcContext();
      this.calcBars();
    }
  },
  async mounted() {
    // await this.$store.dispatch('pull');
    // await this.$store.dispatch('getTimeseries');

  }
};
</script>

