import { reduce } from "lodash";
import { countries, dates } from "../../public/data/summary.json";

const dateMap = reduce(
  dates,
  (o, e, i) => {
    o[e] = i;
    return o;
  },
  {}
);

const categories = ["country", "date", "dateIndex"];

const measures = [
  "total",
  "dTotal",
  "d2Total",
  "deceased",
  "dDeceased",
  "d2Deceased",
  "recovered",
  "dRecovered",
  "d2Recovered",
  "active",
  "dActive",
  "d2Active"
];

const fields = categories.concat(measures);

const modeHeadingMap = {
  0: "Total",
  1: "Total",
  2: "New"
};

const sorterMap = {
  0: "none",
  1: "desc",
  2: "asc"
};

const columns = [
  {
    tag: "country",
    field: "country",
    title: "Country"
  },
  {
    tag: "total",
    field: "total",
    title: "Confirmed",
    dField: "dTotal"
  },
  {
    tag: "deceased",
    field: "deceased",
    title: "Deceased",
    dField: "dDeceased"
  },
  {
    tag: "recovered",
    field: "recovered",
    title: "Recovered",
    dField: "dRecovered"
  },
  {
    tag: "active",
    field: "active",
    title: "Active",
    dField: "dActive"
  }
];

const numberFormatter = new Intl.NumberFormat().format;

const nFormat = number => numberFormatter(number);
const dFormat = number => {
  let n = numberFormatter(number)
  return number <= 0 ? n : `+${n}`;
}

export { categories, countries, dates, dateMap, fields, measures, modeHeadingMap, sorterMap, columns, nFormat, dFormat };
