<template>
  <div id="app">
    <div class="app-header">      
      <div :style="columnWidthStyle(index)" :key="index" v-for="(column, index) in columns">
        <div v-if="index === 0" class="mode-select">
          <span class="symbol" :data-select="mode === 0" title="Total &amp; New Cases" @click="setMode(0)">∑+Δ</span>
          <span class="symbol" :data-select="mode === 1" title="Total Cases" @click="setMode(1)">∑</span>
          <span class="symbol" :data-select="mode === 2" title="New Cases (daily)" @click="setMode(2)">Δ</span>
        </div>
        <div v-else class="indicator">
          <div class="numbers">
            <span :class="column.field">{{ nFormat(currents[column.field]) }}</span>
            <span class="divider"></span>
            <span :class="column.dField">{{ dFormat(currents[column.dField]) }}</span>
          </div>
          <div class="heading">
            {{ column.title }} Cases
          </div>
        </div>
      </div>
      <div class="indicator date">
          <div class="human-date">
            {{ humanDate }}
          </div>
          <div class="heading">
            as {{ distDate }}
          </div>
      </div>
      <div class="countries">
          <span class="country" :key="country" v-for="country in currentCountries">
            {{ country }}
          </span>
      </div>
    </div>
    <div class="app-body">
    <Grid
      id="grid"
      :mode="mode"
      :labelWidth="labelWidth"
      :barWidths="barWidths" 
      @change="gridChange"     
      @resetCountry="resetCountry"
      @toggleCountry="toggleCountry"     
     />
    <div id="charts">
      <Chart
        title="Total Confirmed"
        :selectedMeasures="['deceased','recovered','active']"
        :height="150"
        :width="600"
        />
      <Chart
        title="Daily Deceased"
        :selectedMeasures="['dDeceased']"
        :height="80"
        :width="600"
        />
      <Chart
        title="Daily Recovered"
        :selectedMeasures="['dRecovered']"
        :height="80"
        :width="600"
        />
      <Chart
        title="Daily Active"
        :selectedMeasures="['dActive']"
        :height="80"
        :width="600"
        />
    </div>
    </div>
  </div>
</template>

<script>

import { mapGetters } from 'vuex'
import Grid from './components/Grid.vue';
import Chart from './components/Chart.vue';
import { assign, each, without, union } from 'lodash';
import moment from 'moment';
import { mixin as columnMixin } from './mixins/columns';
import { dates } from '../public/data/summary.json';
import { dateMap, fields, measures, modeHeadingMap, sorterMap, columns, nFormat, dFormat } from './libs/common';

export default {
  name: 'App',
  mixins: [columnMixin],
  components: {
    Grid, Chart,
  },
  data () {
    return {
      humanDate: '',
      distDate: '',
      columns,
      labelWidth: 45,
      mode: 0,
      barWidths: [70, 60, 60, 60, 60],
      selectedCountries: [],
      colWidths: [],
      currents: {
        total: 0,
        dTotal: 0,
        deceased: 0,
        dDeceased: 0,
        recovered: 0,
        dRecovered: 0,
        active: 0,
        dActive: 0,
      }
    }
  },
  computed: {
    ...mapGetters([
      'rows',
      'rowStats',
      'series',
      'currentDate',
      'currentCountries'
    ])
  },
  async created() {
    await this.$store.dispatch('pull');
    this.$store.dispatch('getSnapshot');
    this.$store.dispatch('getTimeseries');
    
  },
  watch: {
    currentDate () {
      this.updateCurrentDate();
    },
    rowStats () {
      this.updateMainStats();   
    }
  },
  mounted() {
  },
  methods: {
    updateCurrentDate () {
      let m = moment(this.currentDate);
      this.humanDate = m.format("ddd, D MMM YY");
      let distDate = moment.duration(m.diff(moment())).asDays()
      if (distDate === 0) {
        distDate = "today"
      } else {
        distDate = moment.duration(-Math.abs(distDate), "days").humanize(true);
      }
      this.distDate = distDate;

    },
    updateMainStats () {
      each(this.currents, (v, field) => {
        this.currents[field] = this.rowStats[field].sum
      })
    },
    nFormat,
    dFormat,
    setMode(mode) {
      this.mode = mode;
    },
    gridChange(stats) {   
      
    },
    resetCountry() {
      this.$store.dispatch('updateCountries', null);
    },
    toggleCountry(country) {
      this.$store.dispatch('updateCountries', country);
    }   
  }
};
</script>

